import { format } from "date-fns";

// Import interfaces
import { Timeline as ITimeline } from "../helpers/interfaces";

// Import helpers
import { TIME_FORMAT, generateArray, generateTimelineSlots } from "../helpers";

type UseTimelineProps = ITimeline;

export function useTimeline(props: UseTimelineProps) {
  const { isBaseTimeFormat, isRTL, isSidebar, isVerticalMode } = props;
  const { dayWidth, hourWidth, sidebarWidth } = props;
  const {
    startDate,
    endDate,
    liveRefreshTime,
    days,
    numberOfDays,
    numberOfHoursInDay,
    offsetStartHoursRange,
    renderCurrentTime,
  } = props;

  const time = generateTimelineSlots({
    isBaseTimeFormat,
    days,
    numberOfDays,
    numberOfHoursInDay,
    offsetStartHoursRange,
  }) as (string | number)[];

  const dividers = generateArray(4);

  // Handlers
  const getTime = (index: number | string) => {
    if (typeof index === "string") return { time: index, isNewDay: true };

    const date = new Date();
    const baseDate = format(date, TIME_FORMAT.DATE);
    const time = index < 10 ? `0${index}` : index;

    if (isBaseTimeFormat) {
      const date = new Date(`${baseDate}T${time}:00:00`);
      const baseFormat = format(date, TIME_FORMAT.BASE_HOURS_TIME)
        .toLowerCase()
        .replace(/\s/g, "");

      return { time: baseFormat, isNewDay: false };
    }

    return { time: `${time}:00`, isNewDay: false };
  };

  const getCurrentTimeProps = () => ({
    isBaseTimeFormat,
    isVerticalMode,
    isRTL,
    startDate,
    endDate,
    dayWidth,
    hourWidth,
    sidebarWidth,
    liveRefreshTime,
    renderCurrentTime,
  });

  const getTimelineProps = () => ({
    isSidebar,
    isVerticalMode,
    dayWidth,
    sidebarWidth,
  });

  return { time, dividers, getTime, getTimelineProps, getCurrentTimeProps };
}
