import * as React from "react";

// Import types
import { CurrentTime as ICurrentTime } from "../helpers/interfaces";
import { DateTime } from "../helpers/types";

// Import styles
import { CurrentTimeStyled } from "../styles";

// Import hooks
import { useLine, useCurrentTime } from "../hooks";

interface CurrentTimeProps {
  isBaseTimeFormat?: boolean;
  isVerticalMode?: boolean;
  isRTL?: boolean;
  startDate: DateTime;
  endDate: DateTime;
  dayWidth: number;
  hourWidth: number;
  sidebarWidth: number;
  liveRefreshTime: number;
  renderCurrentTime?: (v: ICurrentTime) => React.ReactElement;
}

const { CurrentTimeBox, CurrentTimeContent } = CurrentTimeStyled;

export function CurrentTime({
  isVerticalMode,
  isBaseTimeFormat,
  isRTL,
  startDate,
  endDate,
  dayWidth,
  hourWidth,
  sidebarWidth,
  liveRefreshTime,
  renderCurrentTime,
}: CurrentTimeProps) {
  const { positionX } = useLine({
    startDate,
    endDate,
    dayWidth,
    hourWidth,
    sidebarWidth,
    liveRefreshTime,
  });
  const { time, getCurrentTimeStyles } = useCurrentTime({
    isVerticalMode,
    isBaseTimeFormat,
    positionX,
  });

  const styles = getCurrentTimeStyles();

  if (renderCurrentTime) {
    return renderCurrentTime({
      isVerticalMode,
      isBaseTimeFormat,
      isRTL,
      time,
      styles,
    });
  }

  return (
    <CurrentTimeBox {...styles.position}>
      <CurrentTimeContent
        isVerticalMode={isVerticalMode}
        isBaseTimeFormat={isBaseTimeFormat}
        isRTL={isRTL}
      >
        {time}
      </CurrentTimeContent>
    </CurrentTimeBox>
  );
}
