import React from "react";
import { startOfToday } from "date-fns";

// Import interfaces
import { Channel, Program, Theme } from "../helpers/interfaces";

// Import types
import { DateTime, BaseTimeFormat, Position } from "../helpers/types";

// Import helpers
import {
  DAY_WIDTH,
  ITEM_HEIGHT,
  ITEM_OVERSCAN,
  SIDEBAR_WIDTH,
  LIVE_REFRESH_TIME,
  formatTime,
  getConvertedChannels,
  getConvertedPrograms,
  getItemVisibility,
  getSidebarItemVisibility,
  getDayWidthResources,
  getTimeRangeDates,
  getDayResources,
  getTimelineItemVisibility,
} from "../helpers";

// Import theme
import { theme as defaultTheme } from "../theme";

// Import components
import { useLayout } from "./useLayout";

interface useEpgProps {
  isVerticalMode?: boolean;
  isRTL?: boolean;
  isBaseTimeFormat?: BaseTimeFormat;
  isSidebar?: boolean;
  isTimeline?: boolean;
  isLine?: boolean;
  isCurrentTime?: boolean;
  channels: Channel[];
  epg: Program[];
  width?: number;
  height?: number;
  startDate?: DateTime;
  endDate?: DateTime;
  liveRefreshTime?: number;
  theme?: Theme;
  globalStyles?: string;
  dayWidth?: number;
  sidebarWidth?: number;
  itemHeight?: number;
  itemOverscan?: number;
}

const defaultStartDateTime = formatTime(startOfToday());

export function useEpg({
  isVerticalMode = false,
  isRTL = false,
  isBaseTimeFormat = false,
  isSidebar = true,
  isTimeline = true,
  isLine = true,
  isCurrentTime = false,
  width,
  height,
  channels: channelsEpg,
  epg,
  startDate: startDateInput = defaultStartDateTime,
  endDate: endDateInput = "",
  liveRefreshTime = LIVE_REFRESH_TIME,
  theme: customTheme,
  globalStyles,
  dayWidth: customDayWidth = DAY_WIDTH,
  sidebarWidth = SIDEBAR_WIDTH,
  itemHeight = ITEM_HEIGHT,
  itemOverscan = ITEM_OVERSCAN,
}: useEpgProps) {
  // Get converted start and end dates
  const { startDate, endDate } = getTimeRangeDates(
    startDateInput,
    endDateInput
  );

  // Get days resources eg. number of days, dates and hours
  const { isToday, currentDate, ...daysResources } = React.useMemo(
    () => getDayResources({ startDate, endDate }),
    [startDate, endDate]
  );

  // Get day and hour width of the day
  const { hourWidth, dayWidth, ...dayWidthResourcesProps } = React.useMemo(
    () =>
      getDayWidthResources({ dayWidth: customDayWidth, startDate, endDate }),
    [customDayWidth, startDate, endDate]
  );

  // -------- Effects --------
  const { containerRef, scrollBoxRef, ...layoutProps } = useLayout({
    startDate,
    endDate,
    sidebarWidth,
    width,
    height,
    hourWidth,
    isToday,
    currentDate,
    isVerticalMode,
  });

  const { scrollX, scrollY, layoutWidth, layoutHeight } = layoutProps;
  const { onScroll, onScrollToNow, onScrollTop, onScrollLeft, onScrollRight } =
    layoutProps;

  //-------- Variables --------
  const channels = React.useMemo(
    () => getConvertedChannels(channelsEpg, itemHeight),
    [channelsEpg, itemHeight]
  );

  const startDateTime = formatTime(startDate);
  const endDateTime = formatTime(endDate);
  const programs = React.useMemo(
    () =>
      getConvertedPrograms({
        data: epg,
        channels,
        startDate: startDateTime,
        endDate: endDateTime,
        itemHeight,
        hourWidth,
        isVerticalMode,
      }),
    [
      epg,
      channels,
      startDateTime,
      endDateTime,
      itemHeight,
      hourWidth,
      isVerticalMode,
    ]
  );

  const theme: Theme = customTheme || defaultTheme;

  // -------- Handlers --------
  const isProgramVisible = React.useCallback(
    (position: Position) =>
      getItemVisibility({
        position,
        scrollY,
        scrollX,
        containerHeight: layoutHeight,
        containerWidth: layoutWidth,
        itemOverscan,
        isVerticalMode,
      }),
    [scrollY, scrollX, layoutHeight, layoutWidth, itemOverscan, isVerticalMode]
  );

  const isChannelVisible = React.useCallback(
    (position: Pick<Position, "top">) =>
      getSidebarItemVisibility({
        position,
        scrollX,
        scrollY,
        containerHeight: layoutHeight,
        containerWidth: layoutWidth,
        itemOverscan,
        isVerticalMode,
      }),
    [scrollY, scrollX, layoutHeight, layoutWidth, itemOverscan, isVerticalMode]
  );

  const isTimelineVisible = React.useCallback(
    (position: Pick<Position, "left" | "width">) =>
      getTimelineItemVisibility({
        position,
        scrollY,
        scrollX,
        containerHeight: layoutHeight,
        containerWidth: layoutWidth,
        isVerticalMode,
      }),
    [scrollY, scrollX, layoutHeight, layoutWidth, isVerticalMode]
  );

  const getEpgProps = () => ({
    isVerticalMode,
    isRTL,
    isSidebar,
    isLine,
    isTimeline,
    width,
    height,
    sidebarWidth,
    ref: containerRef,
    theme,
    globalStyles,
  });

  const getLayoutProps = () => ({
    programs,
    channels,
    startDate,
    endDate,
    liveRefreshTime,
    scrollY,
    onScroll,
    isVerticalMode,
    isRTL,
    isBaseTimeFormat,
    isSidebar,
    isTimeline,
    isLine,
    isCurrentTime,
    isProgramVisible,
    isChannelVisible,
    isTimelineVisible,
    dayWidth,
    hourWidth,
    sidebarWidth,
    itemHeight,
    isToday,
    currentDate,
    ...daysResources,
    ...dayWidthResourcesProps,
    ref: scrollBoxRef,
  });

  return {
    getEpgProps,
    getLayoutProps,
    onScrollToNow,
    onScrollTop,
    onScrollLeft,
    onScrollRight,
    scrollY,
    scrollX,
  };
}
