import * as React from "react";

// Import interfaces
import { Line as ILine } from "../helpers/interfaces";

// Import types
import { DateTime } from "../helpers/types";

// Import helpers
import { Layers, getLineStyles } from "../helpers";

// Import styles
import { LineStyled } from "../styles";

// Import components
import { useLine } from "../hooks/useLine";

interface LineProps {
  isTimeline: boolean;
  isVerticalMode: boolean;
  height: number;
  startDate: DateTime;
  endDate: DateTime;
  dayWidth: number;
  hourWidth: number;
  sidebarWidth: number;
  liveRefreshTime: number;
  renderLine?: (v: ILine) => React.ReactNode;
}

const { lineStyles, Box } = LineStyled;

export function Line({
  isTimeline,
  isVerticalMode,
  height,
  startDate,
  renderLine,
  ...rest
}: LineProps) {
  const { positionX } = useLine({
    startDate,
    ...rest,
  });

  const position = {
    height,
    left: positionX,
    zIndex: Layers.Line,
  };

  const styles = getLineStyles({
    isVerticalMode,
    isTimeline,
    lineStyles,
    position,
  });

  if (renderLine) return <>{renderLine({ styles, isVerticalMode })}</>;

  return (
    <Box
      isVerticalMode={isVerticalMode}
      isTimeline={isTimeline}
      {...position}
    />
  );
}
