import React from "react";
import { startOfDay } from "date-fns";

// Import types
import { DateTime } from "../helpers/types";

// Import helpers
import { SECONDS_IN_MINUTE, HOUR_IN_MINUTES, getPositionX } from "../helpers";

// Import hooks
import { useInterval } from ".";

interface useLineProps {
  startDate: DateTime;
  endDate: DateTime;
  dayWidth: number;
  hourWidth: number;
  sidebarWidth: number;
  liveRefreshTime: number;
}

export function useLine({
  startDate,
  endDate,
  dayWidth,
  hourWidth,
  sidebarWidth,
  liveRefreshTime,
}: useLineProps) {
  const initialState =
    getPositionX(
      startOfDay(new Date(startDate)),
      new Date(),
      startDate,
      endDate,
      hourWidth
    ) + sidebarWidth;
  const [positionX, setPositionX] = React.useState<number>(() => initialState);

  const liveIntervalTime = liveRefreshTime * 1000;
  const isDayEnd = positionX <= dayWidth;
  const isScrollX = React.useMemo(
    () => (isDayEnd ? liveIntervalTime : null),
    [isDayEnd, liveIntervalTime]
  );

  useInterval(() => {
    const offset = hourWidth / HOUR_IN_MINUTES;
    const step = offset / SECONDS_IN_MINUTE;
    const positionOffset = step * liveRefreshTime;

    setPositionX((prev) => prev + positionOffset);
  }, isScrollX);

  React.useEffect(() => {
    const date = new Date(startDate);
    const positionX = getPositionX(
      startOfDay(date),
      new Date(),
      startDate,
      endDate,
      hourWidth
    );
    const newPositionX = positionX + sidebarWidth;
    setPositionX(newPositionX);
  }, [startDate, endDate, sidebarWidth, hourWidth]);

  return { positionX };
}
