import * as React from "react";

// Import interfaces
import { Timeline as ITimeline } from "../helpers/interfaces";

// Import styles
import { TimelineStyled } from "../styles";

// Import components
import { CurrentTime } from "./CurrentTime";

// Import hooks
import { useTimeline } from "../hooks";

const {
  TimelineWrapper,
  TimelineBox,
  TimelineTime,
  TimelineDividers,
  TimelineDivider,
} = TimelineStyled;

export function Timeline(props: ITimeline) {
  const { time, dividers, getTime, getTimelineProps, getCurrentTimeProps } =
    useTimeline(props);

  const {
    isToday,
    isBaseTimeFormat,
    isCurrentTime,
    isRTL,
    isTimelineVisible,
    isVerticalMode,
  } = props;
  const { hourWidth } = props;

  // Handlers
  const renderTime = (item: string | number, index: number) => {
    const { isNewDay, time } = getTime(item);
    const position = { left: hourWidth * index, width: hourWidth };
    const isVisible = isTimelineVisible(position);
    if (!isVisible) return null;
    return (
      <TimelineBox
        data-testid="timeline-item"
        key={index}
        isToday={isToday}
        isCurrentTime={isCurrentTime}
        isVerticalMode={isVerticalMode}
        {...position}
      >
        <TimelineTime
          isNewDay={isNewDay}
          isBaseTimeFormat={isBaseTimeFormat}
          isRTL={isRTL}
          isVerticalMode={isVerticalMode}
        >
          {time}
        </TimelineTime>
        <TimelineDividers isVerticalMode={isVerticalMode}>
          {renderDividers(isNewDay)}
        </TimelineDividers>
      </TimelineBox>
    );
  };

  const renderDividers = (isNewDay: boolean) =>
    dividers.map((_, index) => (
      <TimelineDivider
        key={index}
        isVerticalMode={isVerticalMode}
        isNewDay={isNewDay}
        width={hourWidth}
      />
    ));

  return (
    <TimelineWrapper data-testid="timeline" {...getTimelineProps()}>
      {isCurrentTime && isToday && <CurrentTime {...getCurrentTimeProps()} />}
      {time.map((item, index) => renderTime(item, index))}
    </TimelineWrapper>
  );
}
