import React from "react";
import { format } from "date-fns";

// Import types
import { BaseTimeFormat } from "../helpers/types";

// Import helpers
import { TIME_FORMAT } from "../helpers";

// Import styles
import { boxStyles } from "../styles/CurrentTime.styles";

interface useCurrentTimeProps {
  isVerticalMode?: boolean;
  isBaseTimeFormat?: BaseTimeFormat;
  positionX: number;
}

export function useCurrentTime({
  isVerticalMode,
  isBaseTimeFormat,
  positionX,
}: useCurrentTimeProps) {
  const formatTime = () => {
    const date = new Date();
    const baseTime = format(date, TIME_FORMAT.HOURS_MIN);

    if (isBaseTimeFormat) {
      const baseFormat = format(date, TIME_FORMAT.BASE_HOURS_TIME)
        .toLowerCase()
        .replace(/\s/g, "");

      return baseFormat;
    }

    return baseTime;
  };

  const handleGetCurrentTimeStyles = () => {
    const width = isVerticalMode ? boxStyles.width : "auto";
    const commonStyles = {
      position: boxStyles.position,
      width,
      height: boxStyles.height,
    };

    if (isVerticalMode) {
      return {
        position: {
          top: positionX - 111,
          left: boxStyles.top,
          ...commonStyles,
        },
      };
    }

    return {
      position: {
        top: boxStyles.top,
        left: positionX - 100,
        ...commonStyles,
      },
    };
  };

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const time = React.useMemo(() => formatTime(), [positionX, isBaseTimeFormat]);

  return { time, getCurrentTimeStyles: handleGetCurrentTimeStyles };
}
