import React from "react";

// Import interfaces
import { Line as ILine } from "../helpers/interfaces";

// Import types
import { Timeline as ITimeline, CurrentTime } from "../helpers/interfaces";
import {
  ProgramItem,
  ProgramWithPosition,
  ChannelWithPosition,
  DateTime,
  Position,
  BaseTimeFormat,
} from "../helpers/types";

// Import helpers
import { getProgramOptions, isFutureTime } from "../helpers";

// Import styles
import { EpgStyled } from "../styles";

// Import components
import { Timeline, Channels, Program, Line } from "../components";

interface LayoutProps {
  programs: ProgramItem[];
  channels: ChannelWithPosition[];
  startDate: DateTime;
  endDate: DateTime;
  scrollY: number;
  dayWidth: number;
  hourWidth: number;
  days: string[];
  currentDate: string;
  numberOfDays: number;
  numberOfHoursInDay: number;
  offsetStartHoursRange: number;
  sidebarWidth: number;
  itemHeight: number;
  liveRefreshTime: number;
  onScroll: (
    e: React.UIEvent<HTMLDivElement, UIEvent> & { target: Element }
  ) => void;
  isToday: boolean;
  isVerticalMode?: boolean;
  isRTL?: boolean;
  isBaseTimeFormat?: BaseTimeFormat;
  isSidebar?: boolean;
  isTimeline?: boolean;
  isLine?: boolean;
  isCurrentTime?: boolean;
  isProgramVisible: (position: Position) => boolean;
  isChannelVisible: (position: Pick<Position, "top">) => boolean;
  isTimelineVisible: (position: Pick<Position, "left" | "width">) => boolean;
  renderProgram?: (v: {
    program: ProgramItem;
    isRTL: boolean;
    isVerticalMode: boolean;
    isBaseTimeFormat: BaseTimeFormat;
    liveRefreshTime: number;
  }) => React.ReactNode;
  renderChannel?: (v: { channel: ChannelWithPosition }) => React.ReactNode;
  renderTimeline?: (v: ITimeline) => React.ReactNode;
  renderLine?: (v: ILine) => React.ReactNode;
  renderCurrentTime?: (v: CurrentTime) => React.ReactElement;
}

const { ScrollBox, Content } = EpgStyled;

export const Layout = React.forwardRef<HTMLDivElement, LayoutProps>(
  (props, scrollBoxRef) => {
    const { channels, programs, startDate, endDate, liveRefreshTime, scrollY } =
      props;
    const { dayWidth, hourWidth, sidebarWidth, itemHeight } = props;
    const { days, numberOfHoursInDay, numberOfDays, offsetStartHoursRange } =
      props;
    const {
      isVerticalMode = false,
      isRTL = false,
      isSidebar = true,
      isTimeline = true,
      isLine = true,
      isToday,
      isBaseTimeFormat = false,
      isCurrentTime = false,
    } = props;

    const {
      onScroll,
      isProgramVisible,
      isChannelVisible,
      isTimelineVisible,
      renderProgram,
      renderChannel,
      renderTimeline,
      renderLine,
      renderCurrentTime,
    } = props;

    const channelsLength = channels.length;
    const contentHeight = React.useMemo(
      () => channelsLength * itemHeight,
      [channelsLength, itemHeight]
    );
    const isFuture = isFutureTime(endDate);

    const renderPrograms = (program: ProgramWithPosition) => {
      const { position } = program;
      const isVisible = isProgramVisible(position);

      if (isVisible) {
        const options = getProgramOptions(program);
        if (renderProgram)
          return renderProgram({
            program: options,
            isRTL,
            isVerticalMode,
            isBaseTimeFormat,
            liveRefreshTime,
          });
        return (
          <Program
            key={program.data.id}
            isRTL={isRTL}
            isVerticalMode={isVerticalMode}
            isBaseTimeFormat={isBaseTimeFormat}
            program={options}
            liveRefreshTime={liveRefreshTime}
          />
        );
      }
      return null;
    };

    const renderTopbar = () => {
      const props = {
        isVerticalMode,
        isSidebar,
        isRTL,
        isTimelineVisible,
        sidebarWidth,
        startDate,
        dayWidth,
        numberOfHoursInDay,
        renderCurrentTime,
      };
      const timeProps = {
        isToday,
        isBaseTimeFormat,
        isCurrentTime,
        offsetStartHoursRange,
        numberOfHoursInDay,
        hourWidth,
        numberOfDays,
        days,
        startDate,
        endDate,
        liveRefreshTime,
      };
      if (renderTimeline) {
        return renderTimeline({ ...timeProps, ...props });
      }
      return <Timeline {...timeProps} {...props} />;
    };

    return (
      <ScrollBox isRTL={isRTL} ref={scrollBoxRef} onScroll={onScroll}>
        {isLine && isToday && isFuture && (
          <Line
            isVerticalMode={isVerticalMode}
            isTimeline={isTimeline}
            dayWidth={dayWidth}
            hourWidth={hourWidth}
            sidebarWidth={sidebarWidth}
            startDate={startDate}
            endDate={endDate}
            height={contentHeight}
            liveRefreshTime={liveRefreshTime}
            renderLine={renderLine}
          />
        )}
        {isTimeline && renderTopbar()}
        {isSidebar && (
          <Channels
            isVerticalMode={isVerticalMode}
            isRTL={isRTL}
            isTimeline={isTimeline}
            isChannelVisible={isChannelVisible}
            sidebarWidth={sidebarWidth}
            contentHeight={contentHeight}
            channels={channels as ChannelWithPosition[]}
            scrollY={scrollY}
            renderChannel={renderChannel}
          />
        )}
        <Content
          data-testid="content"
          isVerticalMode={isVerticalMode}
          sidebarWidth={sidebarWidth}
          isSidebar={isSidebar}
          isTimeline={isTimeline}
          dayWidth={dayWidth}
          contentHeight={contentHeight}
        >
          {programs.map((program) =>
            renderPrograms(program as ProgramWithPosition)
          )}
        </Content>
      </ScrollBox>
    );
  }
);
